    /* border: 1px solid red; */
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

body {
    margin: 0;
}

#logo {
    height: 100px;
    width: 100px;
}

header {
    background-color: #637A9F;
    padding-bottom: 1rem;
}

#content {
    background-color: #C9D7DD;
    min-height: 100vh;
    padding-top: 5vh;
    padding-bottom: 10vh;
}

#logoDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#logoDiv span {
    color: #E8C872;
    font-weight: 700;
    font-style: italic;
}

.nav {
    display: flex;
    gap: 20px;
}

button {
    padding: 1rem;
    background-color: #C9D7DD;
    font-weight: 700;
    font-size: 1.2em;

    border: 2px solid #637A9F;
    border-radius: 25px;
    color: #637A9F;
}

button:hover {
    background-color: #E8C872;
    color: white;
    border: 2px solid black;
}

header,
#content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.food-img {
    filter: brightness(50%);
    border-radius: 5px;
    width: 85vw;

    object-fit: cover;
    height: 35vh;
    margin: 20px;
}

#firstCard,
#secondCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 20px;

}

#FCtext,
#SCtext {
    position: absolute;
    color: white;
    z-index: 1;

    font-size: 0.9em;
    max-width: 85vw;

    margin: 100px;

    display: flex;
    flex-flow: column;
    align-items: center;
}

#FCtext h3,
#SCtext h3 {
    font-size: 1.3em;
}

#FCtext button,
#SCtext button {
    font-size: 1em;
    padding: 10px;
}

/* menu page */
.menu-item {
    margin-left: 15vw;
    margin-right: 15vw;
}


/* about page */
#about-page-image {
    border-radius: 5px;
    width: 70vw;

    object-fit: cover;
    height: 35vh;
    margin: 20px;
}

#about-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 20vw;
    padding-right: 20vw;
    max-width: 60vw;
}

#rightPara {
    max-width: 60vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}


@media only screen and (min-width: 700px) {

    .food-img {
        height: 70vh;
    }

    #logo {
        width: 10vw;
        height: 10vw;
    }

    #logoDiv span {
        font-size: 1.6vw;
    }

    header {
        flex-direction: row;
        gap: 5vw;
        align-items: center;
        padding-bottom: 0;
    }

    #FCtext h3,
    #SCtext h3 {
        margin-left: 10vw;
        margin-right: 10vw;

        font-size: 4vw;
        text-align: center;
    }

    #FCtext p,
    #SCtext p {
        margin-left: 10vw;
        margin-right: 10vw;

        font-size: 2vw;
        text-align: center;

    }

    #FCtext button,
    #SCtext button {
        font-size: 1.5vw;
        margin-bottom: 10vh;
    }

    /* menu page */
    .menu-item {
        margin-left: 30vw;
        margin-right: 30vw;
    }

    /* about page */
    #about-main {
        flex-direction: row;
        padding-left: 10vw;
        padding-right: 10vw;
        max-width: 80vw;
        align-items:flex-start
    }

    #about-page-image {
        width: 30vw;
    }

    #rightPara, #leftPara {
        margin-left: 5px;
        margin-right: 5px;
        max-width: 30vw;
    }

}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;IAC3B,6EAA6E;AACjF;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,yBAAyB;IACzB,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,gBAAgB;IAChB,gBAAgB;;IAEhB,yBAAyB;IACzB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,uBAAuB;IACvB,kBAAkB;IAClB,WAAW;;IAEX,iBAAiB;IACjB,YAAY;IACZ,YAAY;AAChB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;;IAEvB,aAAa;;AAEjB;;AAEA;;IAEI,kBAAkB;IAClB,YAAY;IACZ,UAAU;;IAEV,gBAAgB;IAChB,eAAe;;IAEf,aAAa;;IAEb,aAAa;IACb,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;;IAEI,gBAAgB;AACpB;;AAEA;;IAEI,cAAc;IACd,aAAa;AACjB;;AAEA,cAAc;AACd;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;;AAGA,eAAe;AACf;IACI,kBAAkB;IAClB,WAAW;;IAEX,iBAAiB;IACjB,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;;AAGA;;IAEI;QACI,YAAY;IAChB;;IAEA;QACI,WAAW;QACX,YAAY;IAChB;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,mBAAmB;QACnB,QAAQ;QACR,mBAAmB;QACnB,iBAAiB;IACrB;;IAEA;;QAEI,iBAAiB;QACjB,kBAAkB;;QAElB,cAAc;QACd,kBAAkB;IACtB;;IAEA;;QAEI,iBAAiB;QACjB,kBAAkB;;QAElB,cAAc;QACd,kBAAkB;;IAEtB;;IAEA;;QAEI,gBAAgB;QAChB,mBAAmB;IACvB;;IAEA,cAAc;IACd;QACI,iBAAiB;QACjB,kBAAkB;IACtB;;IAEA,eAAe;IACf;QACI,mBAAmB;QACnB,kBAAkB;QAClB,mBAAmB;QACnB,eAAe;QACf;IACJ;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,gBAAgB;QAChB,iBAAiB;QACjB,eAAe;IACnB;;AAEJ","sourcesContent":["* {\n    /* border: 1px solid red; */\n    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;\n}\n\nbody {\n    margin: 0;\n}\n\n#logo {\n    height: 100px;\n    width: 100px;\n}\n\nheader {\n    background-color: #637A9F;\n    padding-bottom: 1rem;\n}\n\n#c